import React, { useState, useEffect } from "react";
import SubmissionSuccess from "../pages/Submission";
import FormBuilder4 from "./Formbuilder4";
function Survey({ data, timerDone }) {
  const [isSubmitted, setIsSubmitted] = useState(false);

  useEffect(() => {
    if (timerDone) {
      console.log("timer is done");
      setIsSubmitted(true);
    }
  }, [timerDone]);
  const handleSubmit = (event, answers) => {
    event.preventDefault();

    console.log(answers);
    // Send the answers to the server

    // Set isSubmitted to true after sending the data
    setIsSubmitted(true);
  };

  console.log(data);
  return (
    <>
      {!isSubmitted ? (
        <FormBuilder4 questions={data} handleSubmit={handleSubmit} />
      ) : (
        <SubmissionSuccess />
      )}
    </>
  );
}

export default Survey;
