import {
  Box,
  Card,
  CardContent,
  CircularProgress,
  Container,
} from "@mui/material";
import "./App.css";
import Body from "./components/Body";
import Header from "./components/Header";
import Particle from "./components/Particles";
import SurveyChoice from "./pages/SurveyChoice";
import { makeStyles, useTheme } from "@mui/styles";
import illustration from "./assets/survey-illustration.png";
import { useState, useEffect } from "react";
import { CountdownCircleTimer } from "react-countdown-circle-timer";
import Survey from "./components/Survey";
import responseExample from "./assets/responseExample.json";
import { useMediaQuery } from "@mui/material";
import { useParams } from "react-router-dom";

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    padding: theme.spacing(3),
    background: "#263859",
    // backgroundImage: `url(${wallpaper})`,
    backgroundSize: "cover",

    minHeight: "100vh",
  },
  text: {
    color: "red",
  },
  paragraph: {
    fontSize: "1.2rem",
    [theme.breakpoints.up("md")]: {
      fontSize: "1.5rem",
    },
    [theme.breakpoints.up("lg")]: {
      fontSize: "2rem",
    },
  },
  timer: {
    position: "fixed",
    bottom: 10,
    right: 8,
    zIndex: 100,
    animation: "$move 3s ", // add animation
    // color: "white",
    fontWeight: "bold",
    fontSize: "2rem",
  },
  "@keyframes move": {
    "0%": {
      right: "100%", // start from the right
    },
    "100%": {
      right: 8, // move towards 0
    },
  },
}));
function App() {
  const [userLocation, setUserLocation] = useState(null);
  let data = [];
  const [surveyData, setSurveyData] = useState(null);
  const params = useParams();
  const id = params.id;
  const [surveyError, setSurveyError] = useState(null);
  const [timerDone, setTimerDone] = useState(false);

  // let questions = responseExample.data.questions;

  async function getSurvey(id) {
    try {
      const response = await fetch(
        "https://foshizi.herokuapp.com/api/getsurveyid",
        {
          method: "POST",
          headers: {
            accept: "*/*",
            "Content-Type": "application/json",
          },
          body: JSON.stringify({
            id: id,
          }),
        }
      );
      const data = await response.json();
      return data;
    } catch (error) {
      setSurveyData("error");
      console.log(surveyData);
      console.error(error);
    }
  }

  useEffect(() => {
    async function fetchSurveyId() {
      data = await getSurvey(id);
      if (data.statuds === "bad") {
        setSurveyData("error");
      } else {
        setSurveyData(data.data.questions);
      }
      console.log(data);
    }
    fetchSurveyId();

    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(
        (position) => {
          const { latitude, longitude } = position.coords;
          setUserLocation({ latitude, longitude });
          console.log(latitude, longitude);
        },
        (error) => {
          console.error(error);
        }
      );
    } else {
      console.error("Geolocation is not supported by this browser.");
    }
  }, []);
  const classes = useStyles();
  const theme = useTheme();

  const matchesSm = useMediaQuery((theme) => theme.breakpoints.down("sm"));
  const matchesMd = useMediaQuery((theme) =>
    theme.breakpoints.between("sm", "md")
  );
  const matchesLg = useMediaQuery((theme) => theme.breakpoints.up("lg"));

  const size = matchesSm ? 100 : matchesMd ? 130 : matchesLg ? 170 : 170;
  return (
    <Box
      sx={{
        // background: "#263859",
        minHeight: "100vh",
      }}
    >
      {/* <Particle /> */}
      <Header />
      {surveyData === "error" ? (
        // Display an error message if surveyData is "error"
        <Container>
          <Box
            sx={{
              display: "flex",
              background: "#263859",
              color: "white",
              justifyContent: "center",
              alignItems: "center",
            }}
          >
            <h2>Survey no longer available or closed.</h2>
          </Box>
        </Container>
      ) : surveyData ? (
        // Display the Survey component and the circular progress bar if surveyData is not null and not "error"
        <Box>
          <Survey data={surveyData} timerDone={timerDone} />
          <div className={classes.timer}>
            <CountdownCircleTimer
              size={size}
              isPlaying
              duration={600}
              colors={["#f98f19", "#F56833", "#FF0000"]}
              colorsTime={[100, 50, 0]}
              isGrowing={true}
              onComplete={() => {
                setTimerDone(true);
           
              }}
            >
              {({ remainingTime }) => remainingTime}
            </CountdownCircleTimer>
          </div>
        </Box>
      ) : (
        // Display a CircularProgress component if surveyData is null
        <Container>
          <Box
            sx={{
              minHeight: "50vh",
              display: "flex",
              justifyContent: "center",
              alignItems: "center",
            }}
          >
            <CircularProgress size={200} />
          </Box>
        </Container>
      )}

      {/* <SurveyChoice /> */}

      {/* <Body /> */}
    </Box>
  );
}

export default App;
